___CSS_LOADER_EXPORT___.push([module.id, `.tabs,
.slide {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
    background: linear-gradient(to top, rgba(180, 180, 160, 0.4) 0%, rgba(180, 180, 160, 0.4) 100%),
    url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center/cover;
}

.tabs {
    padding: 5em 0 5em;
    min-height: 60svh;
}

html {
    scroll-behavior: smooth;
}

.tabs-container {
  display: flex;
  position: fixed;
  bottom: 40px;
  left: 0;
  transform: translateX(-100%);
  opacity: 0;
  padding: 0.5rem 1rem 0;
  mix-blend-mode: difference;
  z-index: 100;
  animation: slideFadeIn 0.8s ease-out forwards;
  animation-delay: 0.6s;
}

@keyframes slideFadeIn {
  to {
    transform: translateX(0);
    opacity: 1;
  }
}


.tabs-container--top {
    bottom: 1em;
    opacity: 1;
}

.tab {
    display: flex;
    align-items: center;
    letter-spacing: 0.1rem;
    transition: all 0.5s ease;
    text-decoration: none;
    position: relative;
    box-shadow: 0px 0px 8px 0px #000;
    backdrop-filter: blur(5px);
    background: rgba(46, 46, 46, 0.65);
    transition: bottom 0.3s ease, opacity 0.3s ease;
    z-index: 10;
}

.tabs-container a {
    position: relative;
    right: 0;
    align-items: center;
}
.tabs-container a:hover {
    color: var(--accent);
    background: var(--text);
    transition: all 0.35s ease;
}
.tabs-container a::after,
.tabs-container a::before {
    display: none;
}

.tab-slider {
    position: absolute;
    bottom: 0;
    height: 2px;
    background-color: var(--border);
    transition: left 0.25s ease, width 0.3s ease;
    z-index: 1;
}

/* ===============================
   Mobile TAB ONLY
=============================== */
@media (max-width: 599px) {

  html {
    scroll-behavior: smooth;
  }

  .tabs-container {
    position: fixed;
    bottom: 4.5em;
    left: 0;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    opacity: 0;
    animation: tabFadeIn 0.6s ease forwards;
    z-index: 100;
    background: rgba(0,0,0,0.3);
    backdrop-filter: blur(6px);
  }

  @keyframes tabFadeIn {
    to { opacity: 1; }
  }

  .tab {
    display: block;
    padding: 0.6em 1em;
    margin-bottom: 0.3em;
    color: #fff;
    text-decoration: none;
    background: rgba(60,60,60,0.85);
    font-size: 1.3rem;
    letter-spacing: 0.08em;
  }

  .tab.is-active {
    background: #fff;
    color: #000;
  }

  /* slider */
  .tab-slider {
    position: absolute;
    left: 0;
    height: 2px;
    background: #fff;
    transition: top 0.25s ease, width 0.25s ease;
    pointer-events: none;
  }

  /* sections */
  .slide {
    min-height: 100svh;
  }
}
